.block-2 {
  padding: 40px;
  position: relative;
  text-align: center;
  border-radius: 50%;
}
.block-2 .wrap-icon {
  position: relative;
  width: 80px;
  margin-left: auto;
  margin-right: auto;
  height: 80px;
  display: block;
  border-radius: 50%;
  margin-top: -70px;
  margin-bottom: 30px;
}
.block-2 .wrap-icon > span {
  color: #fff;
  font-size: 2rem;
  text-align: center;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  transform: translate(-50%, -50%);
}
.block-2 h2 {
  font-size: 18px;
  color: #fff;
}
.block-2 p {
  color: rgba(255, 255, 255, 0.7);
}
.block-2.red,
.block-2.red .wrap-icon {
  background-color: #1b8f17;
}
.block-2.yellow,
.block-2.yellow .wrap-icon {
  background-color: #0486db;
}
.block-2.teal,
.block-2.teal .wrap-icon {
  background-color: #ec7c1f;
}

